// app/games/rhyme-time/page.tsx
"use client";
import { useState } from "react";
import { motion } from "framer-motion";
import GameLayout from "@/components/common/GameLayout";

const sampleData = {
  word: "cat",
  options: ["hat", "dog", "bat", "cup"],
  correctIndices: [0, 2],
};

export default function RhymeTimeGame() {
  const [selected, setSelected] = useState<number[]>([]);
  const [score, setScore] = useState(0);

  const handleSelect = (i: number) => {
    setSelected((prev) =>
      prev.includes(i) ? prev.filter((x) => x !== i) : [...prev, i]
    );
  };

  const handleSubmit = () => {
    const correct = selected.filter((i) => sampleData.correctIndices.includes(i));
    setScore(correct.length * 50);
  };

  return (
    <GameLayout title=" Rhyme Time">
      <motion.div
        initial={{ opacity: 0, y: 30 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-center space-y-6"
      >
        <h2 className="text-2xl font-semibold text-indigo-700">
            转专转 注 <span className="text-pink-600 font-bold">{sampleData.word}</span>?
        </h2>

        <div className="flex flex-wrap justify-center gap-4 mt-6">
          {sampleData.options.map((opt, i) => (
            <motion.button
              key={opt}
              onClick={() => handleSelect(i)}
              whileTap={{ scale: 0.9 }}
              className={`px-6 py-3 rounded-full text-lg font-medium border-2 transition-all 
                ${
                  selected.includes(i)
                    ? "bg-pink-500 text-white border-pink-600 shadow-lg"
                    : "bg-white border-gray-300 hover:bg-pink-100"
                }`}
            >
              {opt}
            </motion.button>
          ))}
        </div>

        <button
          onClick={handleSubmit}
          className="mt-8 bg-indigo-600 hover:bg-indigo-700 text-white px-6 py-3 rounded-xl font-semibold shadow-md transition"
        >
          拽 转砖转 
        </button>

        {score > 0 && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="mt-6 text-xl text-green-600 font-bold"
          >
            拽: {score} 拽转 
          </motion.div>
        )}
      </motion.div>
    </GameLayout>
  );
}
